<template>
	<div class='container'>
		<!-- <div class='pay-num-box'>
	    	<span class='pay-num-title'>待支付金额</span>
	    	<span class='pay-num-span'>{{orderInfo.orderRealAmount/100 + '元'}}</span>
	  	</div> -->
