			<div class='order-box' style="padding: 0.16rem 0.24rem 0">
				<div class='order-info-item'>
					<span class='order-info-item-key'>订单号</span>
					<span class='order-info-item-value' style="color: rgba(0,0,0,0.38); font-size: 0.12rem; ">{{orderInfo.orderCode}}<br>{{orderTime}}</span>
				</div>
			</div>
			<div class='order-title'>
				<div class="line1"></div>
				<strong>查询主体信息</strong>
				<div class="line1"></div>
			</div>
			<div class='order-box'>
				<div class='order-info-item'>
