					<span class='order-info-item-value'>{{ownerInfo.userName}}</span>
				</div>
				<div class='order-info-item'>
					<span class='order-info-item-key'>身份证号</span>
					<span class='order-info-item-value'>{{ownerInfo.userIdNumber}}</span>
				</div>
				<div class='order-info-item'>
					<span class='order-info-item-key'>手机号码</span>
					<span class='order-info-item-value'>{{ownerInfo.userPhone}}</span>
				</div>
			</div>
		</div>
		<div class="report-content">
			<div class="report-title"><strong>报告主要包含内容</strong></div>
			<div class="report-content-list">
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
					<p class="report-content-text">风险检测</p>
				</div>
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
					<p class="report-content-text">行为测评</p>
				</div>
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
					<p class="report-content-text">多头借贷</p>
				</div>
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
