				</div>
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
					<p class="report-content-text">拒贷预测</p>
				</div>
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
					<p class="report-content-text">平台注册</p>
				</div>
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
					<p class="report-content-text">逾期信息</p>
				</div>
				<div class="report-content-item">
					<img class="report-content-icon" src="../img/check.png">
					<p class="report-content-text">欺诈失信</p>
				</div>
			</div>
		</div>
		<!-- <div class='close-tip' v-if="count_down != '00:00:00'">
      <span class='count-down'>{{count_down}}</span>
      后未支付将自动关闭此订单
    </div>
    <div class='close-tip' v-else><span class='count-down'>订单已自动关闭，请重新下单</span></div> -->
		<div class='pay-btn' @touchstart='pay'>
			<span class="pay_text">优惠支付&nbsp;</span>
			<span class="pay_num1">{{orderInfo.orderRealAmount/100}}元&nbsp;</span>
			<span class="pay_num2">/原价{{orderInfo.orderRealAmount/100 * 2}}元</span>
		</div>
		<div class="link-box">
			<a class="link_item" href="https://anniu.qiyukf.com/client?k=394edb8ea8c46b5bbe73ed39ad18c3f7&wp=1&gid=912308&robotShuntSwitch=0">
				<img class="link_icon" src="../img/order_cs.png">
				<p class="link_text">联系客服</p>
			</a>
			<div class="line2"></div>
			<router-link class="link_item" to="/report">
				<img class="link_icon" src="../img/order_orderlist.png">
				<p class="link_text">历史报告</p>
			</router-link>
		</div>
		<div id="urlBox"></div>
		<div class="form" id="form"></div>
	</div>
</template>

<script>
  import $ from 'zhangjia-zepto'
  import api from '../api/apiV4'
  import md5 from 'js-md5'
  import { callbackUrl, jumpUrl, isWechat } from '../api/apiV4'

  export default {
    name: 'pay',
    data() {
      return {
        payNum: 10,
        orderInfo: {
          orderRealAmount: ''
        },
        orderTime: '',
        ownerInfo: {},
        count_down: '',
        canPay: true,
        timeStamp: 1521709406,
        timer: '',
        appClientObj: {}
      }
    },
    methods: {
      pay() {
        if(this.canPay) {
          var data;
          this.canPay = false
          if(this.orderInfo.orderRealAmount == 0) {
            data = {
              orderId: this.orderInfo.orderId
            }
            api.request('/order/freePay', JSON.stringify(data), 'application/json')
              .then(res => {
                if(res.code == 0) {
                  this.$router.push({
                    name: 'testing3'
                  })
                }
              }).catch(err => {
              console.log(err)
            })
          }else {
            if (isWechat()) {
              // 微信收银台支付
              let data = {
                'callback_url': `${jumpUrl}v3/testing`,
                'mchid': '1527581321',
                'notify_url': `${callbackUrl}order/wechat/payjs/callback`,
                'out_trade_no': this.orderInfo.orderCode,
                'total_fee': parseInt(this.orderInfo.orderRealAmount)
              }
              let stringA = `callback_url=${data.callback_url}&mchid=${data.mchid}&notify_url=${data.notify_url}&out_trade_no=${data.out_trade_no}&total_fee=${data.total_fee}&key=PDXOdDsQVBx9M8p6`
              data.sign = md5(stringA).toUpperCase()
              window.location.href = `https://payjs.cn/api/cashier?callback_url=${data.callback_url}&mchid=${data.mchid}&notify_url=${data.notify_url}&total_fee=${data.total_fee}&out_trade_no=${data.out_trade_no}&sign=${data.sign}`
            } else {
              // 阿里支付
              data = {
                payWay: 'ALIPAY',
                orderId: this.orderInfo.orderId,
                returnUrl: `${jumpUrl}v3/testing`,
                deviceType: 'wap'
              }
              api.request('/order/getPayCodeV3', JSON.stringify(data), 'application/json')
                .then(res => {
                  if(res.code == 0) {
                    $('#form').html(res.data);
                  }
                }).catch(err => {
                console.log(err)
              })
						}
          }
        }else {
          this.$message({
            text: '请勿重复点击',
            type: 'warn'
          })
        }
      },
    },
    created() {
      let that = this;
      this.ownerInfo = JSON.parse(JSON.parse(localStorage.lastOrderInfo)[0].orderSubject);
      if(localStorage.lastOrderInfo) {
        this.orderInfo = JSON.parse(localStorage.lastOrderInfo)[0]
        this.orderTime = api.formatTime(new Date(this.orderInfo.orderTime || this.orderInfo.createTime), true, '-').split(' ')[0]
      }
      let ua = navigator.userAgent
			console.log(ua)
    },
    beforeDestroy() {

    }
  }
</script>

<style scoped>

	.pay-num-box {
		width: 100%;
		height: 1rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		background: #fff;
	}
	.pay-num-title {
		margin-top: 0.16rem;
		font-family: PingFangSC-Regular;
		font-size: 0.13rem;
		color: rgba(0,0,0,0.54);
		line-height: 0.2rem;
	}
	.pay-num-span {
		font-family: PingFangSC-Regular;
		font-size: 0.34rem;
		color: #F37121;
		line-height: 0.51rem;
	}
	.order-item {
		width: 100%;
	}
	.order-title {
		margin-top: 0.08rem;
		padding: 0 0.24rem;
		height: 0.2rem;
		line-height: 0.2rem;
		font-family: PingFangSC-Medium;
		font-size: 0.15rem;
		color: rgba(0,0,0,0.87);
		display: flex;
		justify-content: space-between;
		align-items: center;
		display: -webkit-flex;
		-webkit-justify-content: space-between;
		-webkit-align-items: center;
	}
	.line1 {
		flex: 1;
		height: 0;
		border-top: 0.01rem solid rgba(0,0,0,0.12);
	}
	.order-title strong {
		padding: 0 0.16rem;
	}
	.order-box {
		width: 100%;
		padding: 0.16rem 0.16rem 0.08rem;
