		color: rgba(0,0,0,0.87);
		line-height: 0.2rem;
	}
	.count-down {
		color: #E64340;
	}
	.form {
		height: 0;
		overflow: hidden;
	}
	.report-content {
		margin: 0 auto;
		margin-top: 0.24rem;
		padding: 0.16rem;
		width: 3.28rem;
		box-sizing: border-box;
		background: #F5F5F5;
		border-radius: 0.02rem;
	}
	.report-title {
		font-family: PingFangSC-Medium;
		font-size: 0.16rem;
		color: rgba(0,0,0,0.87);
		line-height: 0.22rem;
		text-align: center;
	}
	.report-content-list {
		margin-top: 0.16rem;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		display: -webkit-flex;
		-webkit-justify-content: space-between;
		-webkit-flex-wrap: wrap;
	}
	.report-content-item {
		margin-right: 0.32rem;
		margin-bottom: 0.08rem;
		display: flex;
		-webkit-display: flex;
	}
	.report-content-item:nth-child(3n) {
		margin-right: 0;
	}
	.report-content-icon {
		width: 0.16rem;
