	}
	.link-box {
		margin-top: 0.32rem;
		display: flex;
		justify-content: center;
		display: -webkit-flex;
		-webkit-justify-content: center;
	}
	.link_item {
		display: flex;
		display: -webkit-flex;
		align-items: center;
