export const formatTime = (date, all, separator) => {
  var year = date.getFullYear()
  var month = date.getMonth() + 1
  var day = date.getDate()

  var hour = date.getHours()
  var minute = date.getMinutes()
  var second = date.getSeconds()

  if (all) {
    return [year, month, day].map(formatNumber).join(separator) + ' ' + [hour, minute, second].map(formatNumber).join(':')
  } else {
    return [month, day].map(formatNumber).join(separator) + ' ' + [hour, minute].map(formatNumber).join(':')
  }

}

export const formatNumber = (n) => {
  n = n.toString()
  return n[1] ? n : '0' + n
}

export const getScript = (url, callback) => {
  let head = document.getElementsByTagName('head')[0],
    js = document.createElement('script');

  js.setAttribute('type', 'text/javascript');
  js.setAttribute('src', url);

  head.appendChild(js);

  let callbackFn = function () {
    if (typeof callback === 'function') {
      callback();
    }
  }
  js.onload = function () {
    callbackFn();
  }
}

