<template>
	<!--pages/agreement/agreement.wxml-->
<div class='container text-box'>
  <div><span class='qa-title'>{{title}}</span></div>
  <div v-for="item in qaList" :key="item.q">
    <div class="qa-pannel">
      <div>
        <span class='qa-q'>{{item.q}}</span>
      </div>
      <div>
        <span class='qa-a'>{{item.a}}</span>
      </div>
      <div>
        <span class='qa-tips'>{{item.tips}}</span>
      </div>
    </div>
  </div>
</div>
</template>

<script>
	export default {
		name: 'agreement',
		data() {
			return {
				title:"常见问题",
			    qaList:[{
			      q:"为什么可以查黑名单记录？",
			      a:"各类提供金融服务的机构会将有逾期、欺诈、失信等负面行为的用户记录，依法上报至合作的金融风险信息共享平台。",
			      tips:"黑名单123的黑名单检测服务，付费接入国内主流的多家金融风险信息共享平台，查询结果覆盖全国网贷、消费金融等多行业黑名单数据。"
			    }, 
			    {
			      q: "有黑名单记录对我影响大吗？",
			      a: "非常大！将直接影响您包括贷款、信用租借、信用免押在内的各类信用生活服务的体验。",
			      tips: "黑名单123的黑名单检测服务，将根据您的多维度检测结果，提供针对性优化建议，辅助您修复信用，降低影响"
			    }, 
			    {
			      q: "有了不良记录，所有信用服务的使用都受影响么？",
			      a: "不一定！不同金融风险信息共享平台接入的金融服务机构不完全一样，不同金融服务机构的客户定位和风控要求也不一样，您可以多试几家提供所需服务的机构。",
			      tips: "黑名单123的黑名单检测服务，综合多维度信息，科学评估不良记录影响范围。"
			    }, 
			    {
			      q: "自我感觉没有什么不良记录，还有没有必要查？",
			      a: "有必要！有以下4点原因：\n1.	身份信息存在冒用风险，如有人冒用您的信息，产生的不良记录将关联在您名下\n2.	还款方式不同，款项到达金融机构的账户时间不同，有可能无意中产生逾期记录\n3.	少数机构会把提前还款和贷款展期作为负面信用记录\n4.	积极了解自己的信用情况，有助于您后续的金融活动中使用信用和提升信用",
			      tips: ""
			    }]
			}
		}
	}
</script>

<style>
	/* pages/agreement/agreement.wxss */
/* pages/qa/qa.wxss */
.text-box {
  height: auto;
  padding: 0.12rem;
}
.qa-title{
  width: auto;
  display: inline-block;
  font-size: 0.16rem;
  padding:0.1rem;
  border-bottom:1px solid #F6871F;
  margin-bottom: 0.2rem;
}
.qa-q, .qa-a, .qa-tips{
  font-size: 0.13rem;
}
.qa-q{
  font-weight:bold;
}
.qa-tips{
  color:#F6871F;
}
.qa-pannel{
  padding-bottom: 0.15rem;
}
</style>


