<template>
	<div class='container'>
	  	<div class='order-item'>
	    	<div class='order-title'>订单信息</div>
	    		<div class='order-box'>
	      			<div class='order-info-item'>
	        			<span class='order-info-item-key'>服务名</span>
	        			<span class='order-info-item-value'>黑名单风险检测</span>
		      		</div>
		      		<div class='order-info-item'>
		        		<span class='order-info-item-key'>订单金额</span>
		        		<span class='order-info-item-value'>19.9元</span>
		      		</div>
			      	<div class='order-info-item'>
			        	<span class='order-info-item-key'>订单号</span>
			        	<span class='order-info-item-value'>{{orderDetail.orderId}}</span>
			      	</div>
			      	<div class='order-info-item'>
				        <span class='order-info-item-key'>创建时间</span>
				        <span class='order-info-item-value'>{{orderDetail.createTime}}</span>
			      	</div>
		      		<!--<div class='order-info-item'>-->
		        		<!--<span class='order-info-item-key'>支付方式</span>-->
		        		<!--<span class='order-info-item-value'>微信</span>-->
		     			<!--</div>-->
		      		<div class='order-info-item'>
				        <span class='order-info-item-key'>支付时间</span>
				        <span class='order-info-item-value'>{{orderDetail.createTime}}</span>
		      		</div>
		    	</div>
		    	<div class='order-title'>待查信用主体</div>
		    		<div class='order-box'>
		      			<div class='order-info-item'>
		        			<span class='order-info-item-key'>真实姓名</span>
		        			<span class='order-info-item-value'>{{orderDetail.reportSubject.userName}}</span>
		      			</div>
		      			<div class='order-info-item'>
		        			<span class='order-info-item-key'>身份证号</span>
		        			<span class='order-info-item-value'>{{orderDetail.reportSubject.userIdNumber}}</span>
		      			</div>
		      			<div class='order-info-item'>
		        			<span class='order-info-item-key'>手机号码</span>
		        			<span class='order-info-item-value'>{{orderDetail.reportSubject.userPhone}}</span>
		      			</div>
		    		</div>
		  		</div>
			</div>
		<!--</div>-->
	<!--</div>-->
</template>

<script>
	import api from '../api/apiV4'
	export default {
		name: 'order',
		data() {
			return {
				orderDetail: {
					orderId: '',
					createTimeOrder: '',
					reportSubject: {
						userName: '',
						userIdNumber: '',
						userPhone: ''
					}
				}
			}
		},
		created() {
			this.orderDetail = JSON.parse(localStorage.nowReportResult)
			this.orderDetail.reportSubject = JSON.parse(this.orderDetail.reportSubject)
			// this.orderDetail.createTime = api.formatTime(new Date(this.orderDetail.createTime), false, '-')
			this.orderDetail.createTime = this.orderDetail.createTime.split(':')[0] + ':' + this.orderDetail.createTime.split(':')[1]
		}
	}
</script>

<style>
.pay-num-text {
  font-family: PingFangSC-Regular;
  font-size: 0.34rem;
  color: #F37121;
  line-height: 0.51rem;
}
.order-item {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.order-title {
  padding: 0 0.16rem;
  height: 0.5rem;
  line-height: 0.6rem;
  font-family: PingFangSC-Medium;
  font-size: 0.15rem;
  color: rgba(0,0,0,0.54);
}
.order-box {
  width: 100%;
  padding: 0.16rem;
  background: #fff;
  box-sizing: border-box;
}
.order-info-item {
  margin-top: 0.08rem;
  display: flex;
  justify-content: space-between;
}
.order-box .order-info-item:nth-child(1) {
  margin-top: 0;
}
.order-info-item-key, .order-info-item-value {
  font-family: PingFangSC-Regular;
  font-size: 0.15rem;
  color: rgba(0,0,0,0.54);
  line-height: 0.23rem;
}
.close-tip {
  margin: 0.24rem 0 0.16rem;
  font-family: PingFangSC-Regular;
  font-size: 0.13rem;
  color: rgba(0,0,0,0.87);
  line-height: 0.2rem;
}
.count-down {
  color: #E64340;
}
</style>



