<template>
    <transition name='fade'>
        <div
         v-show="show"
        :class="{
            'loading--fullscreen':mode == 'fullscreen',
            'loading--full':mode == 'full',
            'loading--absolute':mode == 'absolute',
            'loading':mode == 'normal',
            'loading--mask':mask,
            'loading--white': white
        }"
        :style="wrapStyleObject">
            <span class="loading__box">
                <i class="loading__icon"
                    :style="{
                        'width':size + 'px',
                        'height':size + 'px',
                        'border-color':color,
                        'border-right-color':'transparent'
                    }"></i>
                <em class="loading__title" v-if='title'>{{ title }}</em>
            </span>
        </div>
    </transition>
</template>

<script>
    export default {
        props: {
            show:{
                type: Boolean,
                default:false
            },
            mode: {
                type: String,
                default:'normal'
            },
            color:{
                type: String,
                default:'#FF6E40'
            },
            size:{
                type: Number,
                default:24
            },
            autoSize:{
                type: Boolean,
                default:false
            },
            mask:{
                type: Boolean,
                default:false
            },
            title:{
                type: String,
                default:''
            },
            white:{
                type: Boolean,
                default: false
            }
        },
        computed:{
            wrapStyleObject(){
                if(this.autoSize){
                    return {
                        width:this.size + 'px',
                        height:this.size + 'px'
                    }
                }
            }
        }
    }
</script>

<style lang="scss">
    @import "../../../sass/_variables";
    @import "../../../sass/_extends";
    @import "../../../sass/_mixins";

    .loading{
        @extend %loading;
    }
    .loading__box{
        @extend %loading__box;
    }
    .loading__title{
        font-size: 12px;
        font-weight:500;
        padding: 8px 0 0px 0;
        display: block;
        color:$importColor;
    }
    .loading__icon{
        @extend %loading__icon;
    }
    .loading--fullscreen{
        @extend %loading;
        background-color: rgba(0, 0, 0, 0.12);
        position:fixed;
        z-index:$zIndex_loading_full;
        top:0;
        left:0;
        bottom:0;
        width:100%;
        height:auto;
        transition:ease .3s all;
        transform:translate3d(0,0,0);
        .loading__box{
            background-color: rgba(255, 255, 255, 0.33);
        }
    }
    .loading--mask{
        background:rgba(187, 187, 187, 0.37);
    }
    .loading--white {
        background:rgba(255, 255, 255, 1);
    }
    .loading--full{
        @extend %loading;
        position:absolute;
        z-index:$zIndex_loading;
        top:0;
        left:0;
        bottom:0;
        width:100%;
        height:auto;
        transition:ease .3s all;
        transform:translate3d(0,0,0);
    }
    .loading--absolute{
        @extend %loading;
        position:absolute;
        width:100%;
        top:10px;
        z-index:2;
    }

</style>



