const getScript = (url, callback) => {
    let id = document.getElementById(url)
    if(id){
        callback && callback()
        return
    }
    let head = document.getElementsByTagName('head')[0],
        js = document.createElement('script');

    js.setAttribute('type', 'text/javascript');
    js.setAttribute('src', url);
    js.setAttribute('id', url);

    head.appendChild(js);

    let callbackFn = function(){
        if(typeof callback === 'function'){
            callback();
        }
    }
    js.onload = function() {
        callbackFn();
    }
}
const removeScript = (url, callback) => {
    let elem = document.getElementById(url)
    console.log(elem)
    elem.parentNode.removeChild(elem)
}

const rules = {
    IDCard(val){
        return /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(val);
    },
    password(val){
        return /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,18}$/.test(val);
    },
    tel(val) {
        return /^(\+86([\s-])?)?1\d{10}$/.test(val);
    },
    payPassword(val){
        return /^\d{6}$/.test(val);
    },
    payPasswordStrong(val){
        if (/^(\d)\1+$/.test(val)) return false;  // 全一样
         
        let str = val.replace(/\d/g, function($0, pos) {
            return parseInt($0)-pos;
        })
        if (/^(\d)\1+$/.test(str)) return false;  // 顺增
         
        str = val.replace(/\d/g, function($0, pos) {
            return parseInt($0)+pos;
        });
        if (/^(\d)\1+$/.test(str)) return false;  // 顺减
        return true;
    },
    number(val) {
        return /^[0-9]*$/.test(val);
    },
    price(val) {
        return /^[0-9]+([.]{1}[0-9]+){0,1}$/.test(val);
    },
    email(val) {
        return /^(?:\w+\.?\+?)*\w+@(?:\w+\.)+\w+$/.test(val);
    },
    licence(val) {
        return /^[\u4e00-\u9fa5]{1}[a-zA-Z]{1}[a-zA-Z_0-9]{5}$/.test(val);
    }
}

export const formatTime = (date, type) => {
  // var year = date.getFullYear()
  // var month = date.getMonth() + 1
  // var day = date.getDate()

  // var hour = date.getHours()
  // var minute = date.getMinutes()
  // var second = date.getSeconds()

  // if (all) {
  //   return [year, month, day].map(formatNumber).join(separator) + ' ' + [hour, minute, second].map(formatNumber).join(':')
  // } else {
  //   return [month, day].map(formatNumber).join(separator) + ' ' + [hour, minute].map(formatNumber).join(':')
  // }

  if(!date) {
    return '00:00';
  }

  if(type == 'mm:ss') {
    let ss = parseInt(date / 1000 % 60);
    let mm = parseInt(date / 1000 / 60);
    return [mm, ss].map(formatNumber).join(':');
  }
}

export const formatNumber = (n) => {
  n = n.toString()
  return n[1] ? n : '0' + n
}

export const getParams = () => {
    var str=location.href.replace(/#\//, ''); //取得整个地址栏

    var num=str.indexOf("?") 
    str=str.substr(num+1); //取得所有参数   stringvar.substr(start [, length ]

    var arr=str.split("&"); //各个参数放到数组里
    var obj = {};
    var value, name;
    for(var i=0;i < arr.length;i++){ 
        num=arr[i].indexOf("="); 
        if(num>0){ 
            name=arr[i].split('=')[0].split('#');
            value=arr[i].split('=')[1];
                obj[name] = value;
        }
    }
    return obj
}

export {
    isSupportSticky,
    convertRequestURL,
    getScript,
    removeScript,
    getQueryString,
    rules,
    getOS,
    getBrowser
}



