      getKeySum(item) {
        var sum = 0;
        for(var key in item) {
            sum++;
        }
        return sum - 1;
      },
      drawChart2() {
        let { mpos, atm, pc, swipeOther } = this.reportResult_.rejectBankDto;
        let sum = mpos + atm + pc + swipeOther;
        this.contactsCityInfo[0].value = Math.round(mpos / sum * 100);
        this.contactsCityInfo[1].value = Math.round(atm / sum * 100);
        this.contactsCityInfo[2].value = Math.round(pc / sum * 100);
        this.contactsCityInfo[3].value = Math.round(swipeOther / sum * 100);
        // for(var i = 0; i < this.contactsCityInfo.length; i++) {
        //   if(this.contactsCityInfo[i].value) {
        //     this.myChart2 = this.contactsCityInfo[i];
        //     break;
        //   }
        // }
        this.myChart2 = this.contactsCityInfo[0];
        let myChart = echarts.init(document.getElementById('myChart2'));
        myChart.setOption({
          color: this.color,
          grid: {
            x: 'left'
          },
          series: [
              {
                  name:'地区',
                  type:'pie',
                  radius: ['40%', '80%'],
                  avoidLabelOverlap: false,
                  label: {
                      normal: {
                          show: false,
                          position: 'center'
                      },
                      emphasis: {
                          show: false,
                          textStyle: {
                              fontSize: '14',
                              fontWeight: 'bold'
                          }
                      }
                  },
                  labelLine: {
                      normal: {
                          show: true
                      }
                  },
                  data: this.contactsCityInfo
              }
            ]
        });
        myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: 0
        });
        myChart.on('click', params => {
          myChart.dispatchAction({
            type: 'downplay',
            seriesIndex: 0,
            dataIndex: 0
          });
          myChart.dispatchAction({
            type: 'highlight',
            seriesIndex: 0,
            dataIndex: params.dataIndex
          });
          this.myChart2 = params.data;
        })
      },
      drawChart3() {
        let { high, mid, low } = this.reportResult_.rejectBankDto;
        let sum = high + mid + low;
        this.consume[0].value = Math.round(high / sum * 100);
        this.consume[1].value = Math.round(low / sum * 100);
        this.consume[2].value = Math.round(mid / sum * 100);
        this.myChart3 = this.consume[0];
        console.log(this.myChart3)
        let myChart = echarts.init(document.getElementById('myChart3'));
        myChart.setOption({
          color: this.color,
          grid: {
            x: 'left'
          },
          series: [
              {
                  name:'地区',
                  type:'pie',
                  radius: ['40%', '80%'],
                  avoidLabelOverlap: false,
                  label: {
                      normal: {
                          show: false,
                          position: 'center'
                      },
                      emphasis: {
                          show: true,
                          textStyle: {
                              fontSize: '14',
                              fontWeight: 'bold'
                          }
                      }
                  },
                  labelLine: {
                      normal: {
                          show: false
                      }
                  },
                  data: this.consume
              }
          ]
        });
        myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: 0
        });
        myChart.on('click', params => {
          // console.log(params)
          // if(!this.myChart3IsClick) {
          //   this.consume[0]
          // }
          myChart.dispatchAction({
            type: 'downplay',
            seriesIndex: 0,
            dataIndex: 0
          });
          myChart.dispatchAction({
            type: 'highlight',
            seriesIndex: 0,
            dataIndex: params.dataIndex
          });
          this.myChart3 = params.data;
        })
      },
      orderDetail(reportResult) {
        this.$router.push({
          name: 'order4',
          params: {
            orderId: this.orderId
          }
        })
      },
      downLoad() {
        var u = navigator.userAgent;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        // sa.track('downloadClick', {
        //   deviceType: isAndroid ? 'android' : 'ios'
        // }, () => {
        //   location.href = 'https://sensors-api.kongapi.com/t/JU';
        // });
      },
      creditInfoLevel(lever) {
        switch(lever) {
          case '高':
            return '#F0532D';
          case '中':
            return '#F6871F';
          case '低':
            return '#90C159';
        }
      },
      getActiveRecord() {
        if(this.reportResult.highRiskNum > 0) {
          return 'highRiskNumList'
        }
        if(this.reportResult.midRiskNum > 0) {
          return 'midRiskNumList'
        }
        if(this.reportResult.lowRiskNum > 0) {
          return 'lowRiskNumList'
        }
        return 'highRiskNumList'
      },
      gotop() {
