        if($(window).scrollTop() > $('#tab')[0].offsetTop) {
          $(window).scrollTop((2.9 - 0.45) * this.rem, 0);
        }
        this.checkIndex = index;
      },
      fixedTab() {
        let self = this;
        $(window).scroll(() => {
          if($(window).scrollTop() > this.tabOffsetTop) {
            self.isFixedTab = true;
            $('#tab').css({'background-color': '#fff'})
          }else {
            self.isFixedTab = false;
            $('#tab').css({'background-color': ''})
            let conHeight = $('#tab').height()
            $('#swiper-wrapper-self').css({'height': `${conHeight + 1}px`})
            $('.swiper-slide').css({'height': `${conHeight + 1}px`})
          }
        });
      },
      getDetail() {
        let data = {
          reportId : this.$route.query.reportId
        }
        api.request('order/getReportDetailV1', data).then(res => {
          if(res.code == 0) {
            this.reportResult_ = JSON.parse(res.data.reportResult);
            console.log(this.reportResult_)
            this.isSample = api.getParams().isSample == 1;
            this.type = this.isSample ? [
              { name: '全部', href: 'tab1' },
              { name: '风险信息', href: 'tab1' },
              { name: '多头行为', href: 'tab2' },
              { name: '借贷行为', href: 'tab3' }
            ] : [
              { name: '全部', href: 'tab1' },
              { name: '风险信息', href: 'tab1' },
              { name: '多头行为', href: 'tab2' },
              { name: '借贷行为', href: 'tab3' },
              { name: '法院风险记录', href: 'tab4' },
              // { name: '设备风险', href: 'tab5' },
              // { name: '银联风险', href: 'tab6' }
            ];
