              })
              radialIndicator(document.getElementById('radialIndicator2'), {
                barBgColor: 'rgb(252, 221, 175)',
                barColor: '#F9A01B',
                barWidth: 0.2 * this.rem,
                initValue: this.reportResult_.rejectLoanDto.liabilitiesNeed,
                fontColor: 'rgba(0,0,0,0.60)'
              })
