                this.industrySum = entertainment + travel + financial + superiorQuality + consumeOther;
                radialIndicator(document.getElementById('radialIndicator3'), {
                  barBgColor: 'rgb(249, 193, 180)',
                  barColor: '#F0532D',
                  fontSize: 0.12 * this.rem,
                  barWidth: 0.2 * this.rem,
                  initValue: this.reportResult_.rejectBankDto.amount,
                  currentValue: `击败了${this.reportResult_.rejectBankDto.amount}%的用户`,
                  fontColor: 'rgba(0,0,0,0.60)'
                })
                radialIndicator(document.getElementById('radialIndicator4'), {
                  barBgColor: 'rgb(251, 213, 176)',
                  barColor: '#F6871F',
                  fontSize: 0.12 * this.rem,
                  barWidth: 0.2 * this.rem,
                  initValue: this.reportResult_.rejectBankDto.num,
                  currentValue: `击败了${this.reportResult_.rejectBankDto.num}%的用户`,
                  fontColor: 'rgba(0,0,0,0.60)'
                })
                this.drawChart2();
                this.drawChart3();
              }
              $('canvas').css({
                width: '100%',
                height: '100%'
              })
            })
          }
        })
      }
    },
    watch: {
      reportResult() {

      }
    },
    created() {
      this.getDetail()
    },
    mounted() {
      this.$nextTick(() => {
        document.title = this.isSample ? '风险报告' : '风险报告';
