.LOW {
  animation: rotate_100 1s;
  animation-fill-mode: forwards;
}
@keyframes rotate_100 {
  0% {
    transform: rotate(-110deg);
  }
  100% {
    transform: rotate(-100deg);
  }
}
.MIDDLE {
  animation: rotate0 1s;
 animation-fill-mode: forwards;
}
@keyframes rotate0 {
  0% {
    transform: rotate(-110deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.HIGH {
  animation: rotate100 3s normal;
 animation-fill-mode: forwards;
}
@keyframes rotate100 {
  0% {
    transform: rotate(-110deg);
  }
  100% {
    transform: rotate(100deg);
  }
}
