    box-shadow: 0 0.04rem 0.04rem 0 rgba(243,113,33,0.12);
    border-radius: 0.2rem;
    font-family: PingFangSC-Medium;
    font-size: 0.15rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.4rem;
  }
  .fixedTab {
    position: fixed;
    top: 0;
    z-index: 1000;
  }
</style>



