<template>
  <div class="container">
    <div class="h-list" v-if="list.length">
      <div class="item-option" v-for="item in list" :key="item.orderId" v-if="" @click="goDetail(item)">
        <div class="head-con">
          <span class="m-date">{{item.createTimeReport}}</span>
          <span v-if="item.remainingDays > 0" class="remain-day">剩余 <span style="color: #f00;">{{ item.remainingDays }}</span> 天</span>
          <span v-if="item.remainingDays == 0" class="dayout-img"><img src="../img/expired.png" alt=""></span>
        </div>
        <div class="detection">
          <span class="d-dl">{{item.reportList[0].proName}}</span>
          <div class="d-dr">
            <span class="d-payment" :style="{color: item.orderStatus == '40' ? '#90C159' : '#F9A01B'}">{{item.orderStatusStr}}</span>
            <img class="d-image" src="../img/chevron_right_small.png"/>
          </div>
        </div>
        <div class="message">
		        	<span class="m-custom">{{item.orderSubject.userName
		 + '(' + item.orderSubject.userIdNumber + ')'}}</span>
          <span class="m-money">{{item.orderRealAmount / 100}}元</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '../api/apiV4'

  export default {
    name: 'report',
    data() {
      return {
        list: []
      }
    },
    methods: {
      goDetail(order) {
        if (order.remainingDays == 0) {
          this.$message({
            text: '该订单已过期',
            icon: 'error',
            options: 1000,
          })
          return
        }
        if (order.orderStatus == 10) {
          api.request('/order/getOrderDetailV3', JSON.stringify({orderCode: order.orderCode}), 'application/json')
            .then(res => {
              if (res.code == 0) {
                this.lastOrderInfo = res.data;
                localStorage.lastOrderInfo = JSON.stringify(res.data)
                this.$router.push({
                  name: 'index4',
                  params: {
                    // id: order.orderId
                    isPay: 'open'
                  }
                })
              }
            })
        } else if (order.orderStatus == 0) {
          this.$message({
            text: '该订单已关闭',
            icon: 'error'
          })
        } else if (order.orderStatus == 40) {
          this.$router.push({
            name: 'detail4',
            query: {
              reportId: order.reportList[0].reportId,
              isSample: 0
            }
          })
        } else if (order.orderStatus == 11 || order.orderStatus == 20 || order.orderStatus == 30) {
          this.$router.push({
            name: 'testing4',
            params: {
              reportId: order.reportId
            }
          })
        } else {
          this.$message({
            text: '网络错误，请稍后重试',
            icon: 'error'
          })
        }

      },
    },
    created() {
      api.requestGet('/order/device/orderList', {randomId: localStorage.getItem('randomId')})
        .then(res => {
          if (res.code == 0 && res.data.list.length) {
            let arr = res.data.list
            arr.forEach((value, index) => {
              console.log(value.orderSubject)
              value.orderSubject = JSON.parse(value.reportList[0].orderSubject)
              let userIdNumber = value.orderSubject.userIdNumber.substr(6, 8)
              value.orderSubject.userIdNumber = value.orderSubject.userIdNumber.replace(userIdNumber, '********')
              value.createTimeReport = api.formatTime(new Date(value.createTime), false, '-')
              value.createTimeOrder = api.formatTime(new Date(value.createTime), true, '-')
            })
            console.log(arr);
            this.list = arr;
          } else {
            this.$message({
              text: '暂无历史报告',
              type: 'warn'
            })
          }
        }).catch(err => {
        console.log(err)
      })
      // api.request('/order/getMyOrdersV3', JSON.stringify({
      // 	currentPage: 1,
      // 	pageSize: 5
      // }), 'application/json')
      // .then(res => {
      // 	if(res.code == 0 && res.data.list.length) {
      // 		let arr = res.data.list
      // 		arr.forEach((value, index) => {
      // 	    	console.log(value.orderSubject)
      // 	      	value.orderSubject = JSON.parse(value.reportList[0].orderSubject)
      // 	      	let userIdNumber = value.orderSubject.userIdNumber.substr(6, 8)
      // 	      	value.orderSubject.userIdNumber = value.orderSubject.userIdNumber.replace(userIdNumber, '********')
      // 	      	value.createTimeReport = api.formatTime(new Date(value.createTime), false, '-')
      // 	      	value.createTimeOrder = api.formatTime(new Date(value.createTime), true, '-')
      // 	    })
      // 	    console.log(arr);
      // 	    this.list = arr;
      // 	}else {
      // 		this.$message({
      // 			text: '暂无历史报告',
      // 			type: 'warn'
      // 		})
      // 	}
      // }).catch(err => {
      // 	console.log(err)
      // })
    }

  }
</script>

<style scoped>
  .container{
    background-color: #f5f5f5;
  }
  .h-list {
    margin-top: 0.16rem;
    width: 3.44rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .head-con{
    padding: 0.08rem 0;
    border-bottom: solid 1px rgba(0,0,0,0.04);
    position: relative;
    display: flex;
    justify-items: center;
    align-items: center;
    justify-content: space-between;
  }

  .item-option {
    margin-top: 0.08rem;
    width: 100%;
    padding: 0 0.16rem 0.19rem 0.16rem;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 0.04rem;
  }

  .detection {
    margin-top: 0.1rem;
    display: flex;
    justify-content: space-between;
  }

  .d-dl {
    line-height: 0.26rem;
    font-size: 0.17rem;
    color: rgba(0, 0, 0, 0.87);
  }

  .d-dr {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
  }

  .d-payment {
    font-size: 0.15rem;
    color: #F37121;
    line-height: 0.23rem;
    margin-right: 0.25rem;
  }

  .d-image {
    width: 0.24rem;
    height: 0.24rem;
    position: absolute;
    right: -0.1rem;
    top: 0.15rem;
  }

  .message {
    margin-top: 0.04rem;
    display: flex;
    justify-content: space-between;
  }

  .m-custom {
    font-size: 0.13rem;
    color: rgba(0, 0, 0, 0.54);
    line-height: 0.2rem;
  }

  .m-date {
    font-size: 0.13rem;
    color: rgba(0, 0, 0, 0.38);
    line-height: 0.2rem;
  }

  .m-money{
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: rgba(0,0,0,0.87);
    line-height: 22px;
    margin-right: 0.25rem;
  }

  .nomore {
    width: 100%;
    height: 0.2rem;
    position: relative;
  }

  .nomore span {
    position: absolute;
    top: 0;
    left: 40%;
    display: inline-block;
    width: 0.91rem;
    line-height: 0.2rem;
    font-size: 0.13rem;
    color: rgba(0, 0, 0, 0.38);
  }
  .remain-day {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: rgba(0,0,0,0.38);
    text-align: right;
    line-height: 20px;
  }
  .dayout-img{
    position: relative;
  }
  .dayout-img img{
    width: 0.6rem;
    position: absolute;
    right: 0;
    top: -0.18rem;
  }
</style>



