// 身份证号输入处理
function idNoInputHandler(e) {
    if (this.error && this.errorType == 'idcard') {
      	this.error = false;
    	this.errorType = '';
    	this.error_tip = '';
    }


    let valueObj = e.target;
    let withBirthRE1 = /^\d{6}[03-9]$/;
    let withBirthRE2 = /^\d{6}(19|20)$/;
    let withBirthRE3 = /^\d{6}(19[3-9]|20[01])$/;
    let withBirthRE4 = /^\d{6}(19[3-9][0-9]|200[0-9]|201[0-7])$/;
    let withBirthRE5 = /^\d{10}[01]$/;
    let withBirthRE6 = /^\d{10}(0[1-9]|1[012])$/
    let withBirthRE7 = /^\d{12}[0-3]$/
    let withBirthRE8 = /^\d{12}([012][0-9]|3[01])$/


    if(valueObj.value.length < 18) {
      valueObj.value = valueObj.value.replace(/[^\d]/g, '')
    }

    //年份第一位仅支持输入 1 和 2
    if (withBirthRE1.test(valueObj.value)) {
      console.log(1)
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }

    //两位年份规则，仅支持19和20
    if (valueObj.value.length == 8 && !withBirthRE2.test(valueObj.value)) {
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }

    //三位年份规则，仅支持193-199、200、201这些值
    if (valueObj.value.length == 9 && !withBirthRE3.test(valueObj.value)) {
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }

    //四位年份规则
    if (valueObj.value.length == 10 && !withBirthRE4.test(valueObj.value)) {
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }

    //月份首位规则
    if (valueObj.value.length == 11 && !withBirthRE5.test(valueObj.value)) {
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }
    //月份末位规则
    if (valueObj.value.length == 12 && !withBirthRE6.test(valueObj.value)) {
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }

    //日期首位规则
    if (valueObj.value.length == 13 && !withBirthRE7.test(valueObj.value)) {
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }

    //日期末位规则
    if (valueObj.value.length == 14 && !withBirthRE8.test(valueObj.value)) {
      let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
      setTimeout(() => {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
      }, 10)
      this.idcard = proceed;
      return proceed;
    }

    if(valueObj.value.length > 18) {
        valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
    }

    // if (valueObj.value.length == 18 && !isCnNewID(valueObj.value)) {
    //   let proceed = valueObj.value.substr(0, valueObj.value.length - 1);
    //   setTimeout(() => {
    //     valueObj.value = valueObj.value.substr(0, valueObj.value.length - 1)
    //   }, 10)
    //   this.idcard = proceed;
    //   return proceed;
    // }

    this.idcard = valueObj.value;
 }

 function isCnNewID(cid) {
  var arrExp = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];//加权因子
  var arrValid = [1, 0, "X", 9, 8, 7, 6, 5, 4, 3, 2];//校验码
  if (/^\d{17}\d|X$/i.test(cid)){
    var sum = 0, idx;
    for (var i = 0; i < 17; i++) {
      // 对前17位数字与权值乘积求和
      sum += parseInt(cid.substr(i, 1), 10) * arrExp[i];
    }
    // 计算模（固定算法）
    idx = sum % 11;
    // 检验第18为是否与校验码相等
    return arrValid[idx] == cid.substr(17, 1).toUpperCase();
  }else{
    return false;
  }
}


// 检验手机号并暂存
function keepMobileNum(e) {
	this.mobile_num = e.target.value;

	let isMobile = /^1([358][0-9]|4[56789]|6[67]|7[0135678]|9[189])\d{8}$/.test(e.target.value)
	if (e.target.value.length > 0 && !isMobile) {
  		this.error = true;
    	this.errorType = 'mobile';
    	this.error_tip = '手机号错误-请填写正确的手机号';
  		return;
	}
	//重置报错信息
	if (this.error && this.errorType == 'mobile') {
  		this.error = false;
    	this.errorType = '';
    	this.error_tip = '';
	}
}

// 手机号输入处理
function mobileInputHandler (e) {
	if (this.error && this.errorType == 'idcard') {
      	this.error = false;
    	this.errorType = '';
    	this.error_tip = '';
    }
    let pre1RE = /^[02-9]/;
    let pre2RE = /^1[0-2]$/;
    let pre3RE = /^1([358][0-9]|4[56789]|6[67]|7[0135678]|9[189])$/
    let numObj = e.target;


    numObj.value = e.target.value.replace(/[^\d]/g, '')

    if(numObj.value.length > 11) {
      numObj.value = numObj.value.substr(0, numObj.value.length - 1);
    }
    if (pre1RE.test(numObj.value)) {
      numObj.value = ''
      this.mobile_num = "";
      return "";
    }
    if (pre2RE.test(numObj.value)) {
      numObj.value = '1'
      this.mobile_num = "1";
      return "1";
    }
    if (numObj.value.length == 3 && !pre3RE.test(numObj.value)) {
      let mobile_num = numObj.value.substr(0, numObj.value.length - 1);
      setTimeout(() => {
        numObj.value = numObj.value.substr(0, numObj.value.length - 1)
      }, 10)
      this.mobile_num = mobile_num;
      return mobile_num;
    }

    this.mobile_num = numObj.value;
}

// 表单验证
function verifyForm() {
    let nameRE = /^[\u4E00-\u9FA5]{2,5}(?:·[\u4E00-\u9FA5]{2,5})*$/;
    let idNoRE = /^\d{17}([0-9]|X)$/;

    if (!this.name || this.name.length == 0) {
      return { error: true, errorType: "name", error_tip: "请输入真实姓名" }
    }

    if (!nameRE.test(this.name)) {
      return { error: true, errorType: "name", error_tip: "真实姓名格式不正确" }
    }

    if (!this.idcard || this.idcard.length == 0) {

      return { error: true, errorType: "idcard", error_tip: "请输入身份证号" }
    }

    if (!idNoRE.test(this.idcard)) {
      return { error: true, errorType: "idcard", error_tip: "身份证号格式不正确" }
    }

    return;

}

