// 从数组中对象取出单一或多个属性创建新数组
Array.prototype.createArrFromObj = function (keys) {
    var arr = [];
    if (typeof keys == 'string') {
        for (var i = 0; i < this.length; i++) {
            arr.push(this[i][keys]);
        }
    } else if (Array.isArray(keys)) {
        for (var k = 0; k < keys.length; k++) {
            for (var i = 0; i < this.length; i++) {
                arr[i] = arr[i] ? arr[i] : {};
                arr[i][keys[k]] = this[i][keys[k]];
            }
        }
    }
    return arr;
};

//字符串指定开始结束下标替换字符
String.prototype.safeFormat = function (start, end, code) {
    var arr = this.split('');
    for (var i = 0; i < arr.length; i++) {
        if (i >= start && i <= end) {
            arr[i] = code;
        }
    }
    return arr.join('');
};

