<template>
	<div class="popup_box" ref="popueImg">
		<div class="popup">
			<slot></slot>
			<img class="close_btn" src="../img/close.png" @click="closePopup">
		</div>
	</div>
</template>

<script>
	export default {
		props: ['img'],
		computed: {
			popup_img() {
				return this.img;
			}
		},
		methods: {
			closePopup() {
				this.$emit('closeShowPopupImg');
			},
			onTouchMove(e) {
			    // e.preventDefault()
			    // e.stopPropagation()
			    window.event? window.event.cancelBubble = true : e.stopPropagation();
			}
		},
		mounted() {
			this.$refs.popueImg.addEventListener('touchmove', this.onTouchMove, false);
		}
	}
</script>

<style>
	.popup_box {
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0, 0, 0, 0.8);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		z-index: 3;
	}
	.popup {
		position: relative;
		width: 3.28rem;
		height: 2.9rem;
		background: #fff;
	}
	.close_btn {
		position: absolute;
		top: 0.24rem;
		right: 0.24rem;
		width: 0.24rem;
		height: 0.24rem;
	}
</style>


