<template>
  <div class="progress-circle">
    <svg :width="radius" :height="radius" viewBox="0 0 100 100" version="1.1" xmlns="http://www.w3.org/2000/svg">
      <circle class="progress-background" r="50" cx="50" cy="50" fill="transparent" :style="{stroke: baseColor}"/>
      <circle class="progress-bar" r="50" cx="50" cy="50" fill="transparent" :stroke-dasharray="dashArray"
              :stroke-dashoffset="dashOffset" :style="{stroke: barColor}"/>
    </svg>
    <slot></slot>
  </div>
</template>

<script scoped>
  export default {
    props: {
      radius: {
        type: Number,
        default: 100
      },
      percent: {
        type: Number,
        default: 0
      },
      baseColor: {
      	type: String,
        default: '#fff'
      },
      barColor: {
      	type: String,
        default: '#000'
      }
    },
    data() {
      return {
        dashArray: Math.PI * 100
      }
    },
    computed: {
      dashOffset() {
        return (1 - this.percent) * this.dashArray
      }
    },
    created() {
    	
    }
  }
</script>

<style>
  .progress-circle {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
    
  circle {
    stroke-width: 0.18rem;
    transform-origin: center;
  }
  circle.progress-background {
    transform: scale(0.8);
  }
  circle.progress-bar {
    transform: scale(0.8) rotate(-90deg);
  }
</style>


