      //       agl.src  =  'https://fxgate.baidu.com/angelia/fcagl.js?production=_f7L2XwGXjyszb4d1e2oxPybgD';
      //       var  s  =  document.getElementsByTagName('script')[0];
      //       s.parentNode.insertBefore(agl,  s);
      //     })();
      //   })();
      // }
      localStorage.setItem('utm_source', this.GetUrlParam('utm_source'))
      localStorage.setItem('utm_medium', this.GetUrlParam('utm_medium'))

      let payOpen = this.$route.params.isPay
      if(payOpen === 'open'){
        this.slideShow = true
        let userMsg = JSON.parse(JSON.parse(localStorage.lastOrderInfo)[0].orderSubject)
        this.name = userMsg.userName
        this.mobile_num = userMsg.userPhone
        this.idcard = userMsg.userIdNumber
        this.orderInfo = JSON.parse(localStorage.lastOrderInfo)[0]
        this.isPay = true
      }

      if (!(localStorage.getItem('randomId'))) {
        this.setRandom();
      } else {
        this.getReport();
      }

      sa.registerPage({
        $utm_medium: localStorage.getItem('utm_medium'),
        $utm_source: localStorage.getItem('utm_source')
      });
    },
    mounted() {
      sessionStorage.setItem('isLogin', 1);
      document.title = '风险检测'
      let test = 'callback_url=http://h5-black-check.int.anniu-inc.com/testing&mchid=1527581321&notify_url=http://anniu.jspay.com.vaiwan.com/order/wechat/payjs/callback&out_trade_no=201903151622113047468&total_fee=1&key=PDXOdDsQVBx9M8p6'
      let str = md5(test).toUpperCase()
      this.scrollMsg()
      setInterval(this.scrollMsg, 10000)
      sa.track('webview', {appname: 'xyH5_SEM', pagename: 'riskOrderFillPage', PlatformType: 'H5'})
    },
    watch: {},
    beforeDestroy() {
      window.removeEventListener("popstate", function (e) {

      }, false);
    }
  }
