    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.60);
    text-align: center;
    line-height: 20px;
  }

  .intro_text span {
    font-family: PingFangSC-Medium;
    color: #000000;
  }

  .test_btn {
    position: relative;
    margin-top: 0.34rem;
    width: 2.63rem;
    height: 0.36rem;
    background: #F37121;
    border-radius: 0.18rem;
    text-align: center;
    line-height: 0.36rem;
    font-size: 0.15rem;
    color: #fff;
  }

  .custom-service {
    margin-top: 0.24rem;
    font-family: PingFangSC-Regular;
    font-size: 0.12rem;
    color: rgba(0, 0, 0, 0.60);
    text-align: left;
    line-height: 0.18rem;
  }

  .custom-service a {
    color: #F37121;
  }

  .bottom {
    padding: 0;
    padding-top: 0.24rem;
    margin-bottom: 0.24rem;
  }

  .bottom_img {
    width: 100%;
  }

  .bottom-text {
    margin-top: 0.04rem;
    margin-bottom: 0.24rem;
    font-family: PingFangSC-Regular;
    font-size: 0.12rem;
    color: rgba(0, 0, 0, 0.26);
    text-align: left;
    line-height: 0.18rem;
  }

  .slideBox {
    position: fixed;
    top: 1.8rem;
    right: 0;
    width: 0.4rem;
    background: #C3912F;
    border-radius: 0.04rem 0 0 0.04rem;
    z-index: 1000;
  }

  .customer_service_box {
    width: 0.4rem;
    height: 0.53rem;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: -webkit-flex;
    -webkit-flex-direction: column;
    -webkit-justify-content: center;
    -webkit-align-items: center;
  }

  .customer_service {
    width: 0.24rem;
    height: 0.24rem;
  }

  .customer_service_text {
    font-family: PingFangSC-Regular;
    font-size: 0.12rem;
    color: #fff;
    text-align: left;
    line-height: 0.18rem;
  }

  .line3 {
    margin: 0 auto;
    width: 0.16rem;
    border: 1px solid rgba(255, 255, 255, 0.12);
  }

  .check-box {
    width: 2.96rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  .label-test {
    margin-top: 0.16rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .check {
    margin-right: 0.11rem;
    width: 0.15rem;
    height: 0.15rem;
  }

  .read-test-agreement {
    max-width: 3rem;
    font-family: PingFangSC-Regular;
    font-size: 0.13rem;
    color: rgba(0, 0, 0, 0.54);
    line-height: 0.2rem;
  }

  .test-agreement {
    font-family: PingFangSC-Regular;
    font-size: 0.13rem;
    color: rgba(0, 0, 0, 0.87);
    line-height: 0.2rem;
    text-decoration: underline;
  }

  .test-agreement2 {
    font-family: PingFangSC-Regular;
    font-size: 0.13rem;
    color: #F6871F;
    line-height: 0.2rem;
    text-decoration: underline;
  }

  .test-btn {
    margin-top: 0.36rem;
    width: 90%;
    height: 0.36rem;
    background: linear-gradient(to bottom right, #F6871F, #F9A01B);
    box-shadow: 0 0.04rem 0.05rem #F9A01B;
    text-align: center;
    line-height: 0.36rem;
    font-size: 0.17rem;
    color: #fff;
    border-radius: 0.18rem;
  }

  .dialog-bg{
    z-index: 3000;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    margin: 0;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .not-show-bg{
    display: none;
  }

  .dialog{
    z-index: 3001;
    position: fixed;
    top: 25%;
    width: 80%;
    height: auto;
    overflow-y: auto;
    background-color: #fff;
    border-radius: 0.04rem;
    padding: 0.1rem;
  }
  .dialog h3{
    font-size: 0.2rem;
    text-align: center;
    padding: 0.05rem 0;
    margin: 0.1rem 0;
  }

  .dialog-footer{
    width: 100%;
    text-align: center;
  }

  .bot-info{
    font-family: PingFangSC-Regular;
    font-size: 0.12rem;
    color: rgba(0,0,0,0.38);
    text-align: center;
    line-height: 18px;
  }
  .bot-info img{
    position: relative;
    display: inline-block;
    height: 0.13rem;
    width: 0.13rem;
    top: -1px;
    margin-right: 0.06rem;
  }

  .footer-btn{
    margin-top: 0.1rem;
    position: relative;
    display: inline-block;
    font-size: 0.14rem;
    color: #fff;
    background-color: #F37121;
    padding: 0.1rem 0.15rem;
    border-radius: 0.04rem;
    margin-bottom: 0.1rem;
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: top 1.5s;
    top: 0rem;
  }

  .fade-enter{
    top: 0.24rem;
  }
  .fade-leave-to {
    top: -0.24rem;
  }
  .insurance{
    margin-top: 0.12rem;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: rgba(0,0,0,0.38);
    letter-spacing: -0.1px;
    text-align: center;
    line-height: 20px;
  }
  .insurance-img{
    width: 0.66rem;
  }
  .scan-con{
    position: relative;
    margin-top: 0.06rem;
    width: 100%;
    background: #F5F5F5;
    border-radius: 2px;
    margin-bottom: 0.12rem;
    padding: 0.12rem 0.12rem 0 0.12rem;
    box-sizing: border-box;
  }
  .con-tri{
    position: absolute;
    width: 0.1rem;
    height: 0.1rem;
    background: #F5F5F5;
    top: -0.05rem;
    transform:rotate(45deg);
    left: 15%;
  }
  .scan-item{
    position: relative;
    display: inline-block;
    width: 60%;
    font-family: PingFangSC-Regular;
    font-size: 13px;
    color: rgba(0,0,0,0.60);
    letter-spacing: 0.54px;
    text-align: left;
    margin-bottom: 0.12rem;
  }
  .scan-item:nth-child(odd){
    width: 35%;
  }
  .scan-item img{
    position: relative;
    top: -0.02rem;
    margin: 0 0.05rem;
    display: inline-block;
    width: 0.14rem;
  }

  .slide-fade-enter-active {
    transition: all 1.3s ease;
    transition-delay:.5s;
  }
  .slide-fade-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(0px);
    transition-delay:.5s;
    opacity: 0;
  }

  .slide-fade1-enter-active {
    transition: all 1.3s ease;
    transition-delay:1.5s;
  }
  .slide-fade1-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(0px);
    transition-delay:1.5s;
    opacity: 0;
  }

  .slide-fade2-enter-active {
    transition: all 1.3s ease;
    transition-delay:2s;
  }
  .slide-fade2-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(0px);
    transition-delay:2s;
    opacity: 0;
  }

  .slide-fade3-enter-active {
    transition: all 1.3s ease;
    transition-delay:3s;
  }
  .slide-fade3-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(0px);
    transition-delay:3s;
    opacity: 0;
  }

  .slide-fade4-enter-active {
    transition: all 1.3s ease;
    transition-delay:4s;
  }
  .slide-fade4-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(0px);
    transition-delay:4s;
    opacity: 0;
  }

  .slide-fade5-enter-active {
    transition: all 1.3s ease;
    transition-delay:5s;
  }
  .slide-fade5-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(0px);
    transition-delay:5s;
    opacity: 0;
  }


  ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
    color: rgba(0,0,0,0.26);
    text-align: left;
    line-height: 0.2rem;
  }
  :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
    color: rgba(0,0,0,0.26);
    text-align: left;
    line-height: 0.2rem;
  }
  ::-moz-placeholder { /* Mozilla Firefox 19+ */
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
    color: rgba(0,0,0,0.26);
    text-align: left;
    line-height: 0.2rem;
  }
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
    color: rgba(0,0,0,0.26);
    text-align: left;
    line-height: 0.2rem;
  }
  .guide_test {
    position: absolute;
    top: -0.15rem;
    left: -0.08rem;
    padding: 0 0.06rem;
    font-family: PingFangSC-Regular;
    font-size: 0.12rem;
    color: #F37121;
    text-align: left;
    line-height: 0.18rem;
    background: rgb(252, 226, 211);
    border-radius: 0.02rem;
  }
  .guide_test::after {
    content: '';
    position: absolute;
    top: 0.17rem;
    left: 0.24rem;
    width: 0;
    height: 0;
    border: 0.1rem transparent solid;
    border-top-color: rgb(252, 226, 211);
  }
</style>



