<template>
	<div class="container">
    <img class="content" :src="this.isSample ? require('../img/simple.png') : require('../img/complate-v4.jpg')">
  </div>
</template>

<script>
import api from '../api/apiV4'
export default {
  data() {
    return {
      isSample: true
    }
  },
  created() {
    this.isSample = api.getParams().isSample == 1;
  }
}
</script>

<style scoped>
  .content {
    margin-top: -0.8rem;
    width: 100%;
  }
</style>



